import Vue from 'vue'
import VueRouter from 'vue-router'

// import Home from '../components/Home/Home.vue'
// 路由懒加载
const Home = () => import(/* webpackChunkName: "Home_Welcome_Login" */ '../components/Home/Home.vue')
// import Welcome from '../components/Home/Welcome.vue'
const Welcome = () => import(/* webpackChunkName: "home_welcome_Login" */ '../components/Home/Welcome.vue')
// import Login from '../components/Login/Login.vue'
const Login = () => import(/* webpackChunkName: "home_welcome_Login" */ '../components/Login/Login.vue')

// import Vitae from '../components/Vitae/Vitae.vue'
const Vitae = () => import(/* webpackChunkName: "Vitae_PersonalCenter" */ '../components/Vitae/Vitae.vue')
// import PersonalCenter from '../components/PersonalCenter/PersonalCenter.vue'
const PersonalCenter = () => import(/* webpackChunkName: "Vitae_PersonalCenter" */ '../components/PersonalCenter/PersonalCenter.vue')

// import StudyNotes from '../components/StudyNotes/StudyNotes.vue'
const StudyNotes = () => import(/* webpackChunkName: "StudyNotes_NoteContent_MessageBoard" */ '../components/StudyNotes/StudyNotes.vue')
// import NoteContent from '../components/StudyNotes/NoteContent.vue'
const NoteContent = () => import(/* webpackChunkName: "StudyNotes_NoteContent_MessageBoard" */ '../components/StudyNotes/NoteContent.vue')
// import MessageBoard from '../components/MessageBoard/MessageBoard.vue'
const MessageBoard = () => import(/* webpackChunkName: "StudyNotes_NoteContent_MessageBoard" */ '../components/MessageBoard/MessageBoard.vue')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/welcome'
  },
  {
    path: '/home',
    name: 'home',
    component: Home,
    redirect: '/welcome',
    children: [
      {
        path: '/welcome',
        name: 'welcome',
        component: Welcome,
        meta: { title: '如逸O的个人博客——首页' }
      },
      {
        path: '/vitae',
        name: 'vitae',
        component: Vitae,
        meta: { title: '如逸O的个人博客——简历' }
      },
      {
        path: '/studynotes',
        name: 'studynotes',
        component: StudyNotes,
        meta: { title: '如逸O的个人博客——学习笔记' }
      },
      {
        path: '/messageboard',
        name: 'messageboard',
        component: MessageBoard,
        meta: { title: '如逸O的个人博客——留言板' }
      },
      {
        path: '/notecontent',
        name: 'notecontent',
        component: NoteContent,
        meta: { title: '如逸O的个人博客——笔记内容' }
      },
      {
        path: '/personalcenter',
        name: 'personalcenter',
        component: PersonalCenter,
        meta: { title: '如逸O的个人博客——个人中心' }
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: { title: '如逸O的个人博客——Login' }
  }
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  routes
})

// 路由守卫
// beforeEach是router的钩子函数，在进入路由前执行
// eslint-disable-next-line no-use-before-define
router.beforeEach((to, from, next) => {
  // 判断是否有标题并动态设置标题
  if (to.meta.title) {
    document.title = to.meta.title
  } else {
    document.title = '如逸O的个人博客'
  }
  // 执行进入路由，如果不写就不会进入目标页
  next()
})

export default router
